interface Props {
  ref: React.RefObject<HTMLDivElement>;
  containerRef: React.RefObject<HTMLDivElement>;
}

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
export default function useDragScroll({ ref, containerRef }: Props) {
  let position = {
    left: 0,
    x: 0
  };

  const mouseDownHandler = function (e: any) {
    if (ref.current) {
      ref.current.style.cursor = "grabbing";
      position = {
        left: ref.current.scrollLeft,
        x: e.clientX
      };
      ref.current.addEventListener("mousemove", mouseMoveHandler);
      ref.current.addEventListener("mouseup", mouseUpHandler);
    }
  };

  const mouseMoveHandler = function (e: any) {
    e.preventDefault();
    e.stopPropagation();
    const dx = e.clientX - position.x;

    if (ref.current && containerRef.current) {
      const offset = position.left - dx;
      ref.current.scrollTo({ left: offset });
      // containerRef.current.style.transform = "translateX(-" + offset + "px)";
      // containerRef.current.style.left = "translateX(" + offset + "px)";
    }
  };

  const mouseUpHandler = function (e: any) {
    if (ref.current) {
      ref.current.style.cursor = "grab";
    }
    ref.current?.removeEventListener("mousemove", mouseMoveHandler);
  };

  return {
    container: ref.current,
    mouseDownHandler
  };
}
